import proto from "../../../../proto_js/proto.js";
import { Session } from "../../logic/login/LoginModel";
import { Pomelo } from "./Pomelo";


const starx = (window as any).starx
const protocol = (window as any).Protocol
export class PitayaWs {

    private static _ws: WebSocket;

    private static _reqId: number

    public static async Init(host: string, port: number) {

        let handshakeBuffer = {
            'sys': {
                type: "pitaya_cocos",
                version: "1.0.0",
                rsa: {}
            },
            'user': {}
        };

        return new Promise((reslove, reject) => {
            this._ws = new WebSocket('ws://127.0.0.1:3250');
            this._reqId = 0;
            this._ws.binaryType = 'arraybuffer';
            this._ws.onclose = (ev: Event) => {
                console.log("websocket close", ev)
                // reject(ev)
            };
            this._ws.onerror = (ev: Event) => {
                console.log("websocket err", ev)
                // reject(ev)
            };
            this._ws.onmessage = (ev: any) => {
                console.log("websocket onmessage", ev)
                let dispatch = (msg: { type: number, body?: Uint8Array }) => {
                    switch (msg.type) {
                        case Pomelo.Package.TYPE_HANDSHAKE:
                            this.handshake(msg.body)
                            break
                        case Pomelo.Package.TYPE_HEARTBEAT:
                            this.heartbeat(msg.body)
                            break
                        case Pomelo.Package.TYPE_DATA:
                            this.onData(msg.body as ArrayBuffer)
                            break
                        case Pomelo.Package.TYPE_KICK:
                            this.onKick(msg.body)
                            break
                    }
                }
                let msgs = Pomelo.Package.decode(ev);
                if (Array.isArray(msgs)) {
                    for (let i = 0; i < msgs.length; i++) {
                        let msg = msgs[i];
                        dispatch(msg)
                    }
                    return
                }
                dispatch(msgs)
            };

            this._ws.onopen = (ev: Event) => {
                // let handshakePkg = Pomelo.strencode(JSON.stringify(handshakeBuffer))
                // var pkg = Pomelo.Package.encode(Pomelo.Package.TYPE_HANDSHAKE, handshakePkg);
                // this._ws.send(pkg.buffer);

                console.log("websocket open", ev)
                reslove(ev)
            };
        })
    }

    private static handshake(data?: ArrayBuffer) {

    }

    private static heartbeat(data?: ArrayBuffer) {


    }

    private static onKick(data?: ArrayBuffer) {


    }

    private static onData(data: ArrayBuffer) {
        // const msg = Pomelo.Message.decode(data);
        // if (msg.route == "Connector.Handler.CallAuth") {
        //     let resp = proto.lobby.RespAuth.decode(msg.body)
        //     console.log("Connector.Handler.CallAuth, resp=", resp)
        // }
    }


    public static Call(route: string, data: any) {
        console.log(" pitaya call")
        this._reqId++;
        let msg = protocol.Message.encode(this._reqId, Pomelo.Message.TYPE_REQUEST, 0, route, data);
        let pkg = protocol.Package.encode(Pomelo.Package.TYPE_DATA, msg)
        // let msg = Pomelo.Message.encode(this._reqId, Pomelo.Message.TYPE_REQUEST, 0, route, data);
        // let pkg = Pomelo.Package.encode(Pomelo.Package.TYPE_DATA, msg)
        this._ws.send(pkg.buffer);


        // this.testStartX()
    }

    public static CallAuth() {
        console.log(" pitaya call")
        this._reqId++;

        let reqByte = proto.lobby.ReqAuth.encode({ Token: Session.Account.token }).finish()

        let msg = protocol.Message.encode(this._reqId, Pomelo.Message.TYPE_REQUEST, 0, "Connector.Handler.CallAuth", reqByte);
        let pkg = protocol.Package.encode(Pomelo.Package.TYPE_DATA, msg)
        // let msg = Pomelo.Message.encode(this._reqId, Pomelo.Message.TYPE_REQUEST, 0, route, data);
        // let pkg = Pomelo.Package.encode(Pomelo.Package.TYPE_DATA, msg)
        this._ws.send(pkg.buffer);


        // this.testStartX()
    }


    private static testStartX() {
        starx.init(
            {
                host: Session.WsUrl.Host,
                port: Session.WsUrl.Port
            },
            () => {

                starx.request("Connector.Handler.CallAuth", proto.lobby.ReqAuth.encode({ Token: Session.Account.token }).finish(), (e: any) => {
                    let resp = proto.lobby.RespAuth.decode(e);
                    console.log(" Connector.Handler.CallAuth, success resp= ", resp);
                });
            })
    }

}

